<template>
  <div class="btn-list">
    <div v-for="(btn, index) in btnList" :key="index" @click="changeType(btn, index)"
         :class="['btn', activeIndex === index ? 'activeBtn' : '']" :style="withObj">{{btn.name}}</div>
  </div>
</template>

<script>
export default {
  name: 'switchBtnList',
  props: ['btnList'],
  data () {
    return {
      activeIndex: 0
      // btnList: [{ name: '直营' }, { name: '子公司' }, { name: '直营' }, { name: '子公司' }]
    }
  },
  computed: {
    withObj () {
      return { width: (100 / this.btnList.length) + '%' }
    }
  },
  methods: {
    changeType (btn, index) {
      this.activeIndex = index
      this.$emit('changeType', {btn, index})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .btn-list {
    padding: 0 px2rem(32);
    height: px2rem(64);
    display: flex;
    border: none;
    .btn {
      width: 50%;
      height: 100%;
      line-height: px2rem(64);
      border: px2rem(1) solid #24396A;
      border-right: none;
      color: #5F6D96;
      font-size: px2rem(28);
      background-color: #09103D;
      text-align: center;
      vertical-align: middle;
      &:first-child {
        border-radius: px2rem(8) 0 0 px2rem(8);
      }
      &:last-child {
        border-radius: 0 px2rem(8) px2rem(8) 0;
        border-right: px2rem(1) solid #24396A;
      }
    }
    .activeBtn {
      color: #fff;
      background-color: #202D77;
    }
  }
</style>
