<template>
  <div class="pie-container">
    <div :id="id" class="pie-chart"></div>
  </div>
</template>
<script>
import _ from 'lodash'
import echarts from 'echarts'
import { px2px } from '../../assets/js/lib/common/flexible'
export default {
  name: 'RingChart',
  props: {
    finRate: {
      default: '20'
    }
  },
  data () {
    return {
      chart: undefined,
      id: _.uniqueId('pie-')
    }
  },
  watch: {
    finRate () {
      this.drawPie()
    }
  },
  methods: {
    getOption () {
      let option = {
        series: [
          {
            type: 'pie',
            hoverAnimation: false,
            radius: ['55%', '70%'],
            label: {
              position: 'center'
            },
            data: [
              {
                name: '',
                value: this.finRate < 0 ? 0 : this.finRate,
                label: {
                  show: true,
                  textStyle: {
                    color: '#fff',
                    fontWeight: 'bold',
                    fontSize: px2px(30)
                  },
                  formatter: '{c}%'
                },
                itemStyle: {
                  normal: {
                    color: '#34C9F8'
                  }
                }
              },
              {
                name: '',
                value: 100 - this.finRate < 0 ? 0 : 100 - this.finRate,
                label: {
                  show: false,
                  lineHeight: 56,
                  textStyle: {
                    color: '#666',
                    fontSize: px2px(30)
                  }
                },
                tooltip: {
                  show: false
                },
                itemStyle: {
                  normal: {
                    color: '#3D4781'
                  }
                }
              }
            ]
          }
        ]
      }
      return option
    },
    drawPie () {
      // console.log('=== 渲染饼图 ===')
      if (!this.chart) {
        this.chart = echarts.init(document.getElementById(this.id))
      }
      this.chart.setOption(this.getOption())
    }
  },
  mounted () {
    this.drawPie()
  },
  beforeDestroy () {
    if (this.chart) {
      this.chart.clear()
      this.chart.dispose()
      this.chart = undefined
    }
  }
}
</script>

<style lang="scss" scoped>
.pie-container {
  position: relative;
  height:100%;
  width: 100%;
}
.pie-chart {
  height:100%;
}
</style>
