<template>
  <div :class="['zb-card-content', order%3 === 0 ? 'zb-card-content-bg' : '']">
    <div class="zb-name">{{zbObj.zbName}}</div>
    <div class="zb-subname">{{zbObj.subName}}</div>
    <div class="zb-value" v-if="zbObj.type === '1'">
      {{zbObj.firstValue}}
    </div>
    <div class="zb-note" v-if="zbObj.type === '1' && zbObj.showThreeRow">
      <span>{{zbObj.subLabel}}</span><span>{{zbObj.secondValue}}</span>
      <span v-if="zbObj.needArrow" :class="[isFuShu(zbObj.secondValue) ? 'redColor' : 'greenColor']">{{getArrow(zbObj.secondValue)}}</span>
      <span v-if="zbObj.needThreeValue" :class="[isFuShu(zbObj.thirdValue) ? 'redColor' : 'greenColor']">{{zbObj.thirdValue}}</span>
    </div>
    <div class="ring-chart-content" v-if="zbObj.type === '2'">
      <ring-chart :fin-rate="zbObj.firstValue"></ring-chart>
    </div>
    <div class="ring-chart-label" v-if="zbObj.type === '2'">
      <span>{{zbObj.subLabel}}</span><span>{{zbObj.secondValue}}</span>
    </div>
    <div v-if="zbObj.type === '3'" :class="['zb-value3', isFuShu(zbObj.secondValue) ? 'redColor' : 'greenColor']">
      {{zbObj.firstValue}}<span class="">{{getArrow(zbObj.firstValue)}}</span>
    </div>
    <div v-if="zbObj.type === '4'" class="second-row">
      <span>7天: </span> <span>{{zbObj.secondValue}}</span>
    </div>
    <div v-if="zbObj.type === '4'" class="three-row">
      <span>30天: </span> <span>{{zbObj.secondValue}}</span>
    </div>
  </div>
</template>

<script>
import RingChart from '../echarts/RingChart'
export default {
  name: '',
  props: ['zbObj', 'order'],
  data () {
    return {}
  },
  methods: {
    isFuShu (str) {
      str = str + ''
      if (str.indexOf('-') > -1) {
        return true
      } else {
        return false
      }
    },
    getArrow (str) {
      str = str + ''
      if (str.indexOf('-') > -1) {
        return '↓'
      } else {
        return '↑'
      }
    }
  },
  components: {
    RingChart
  }
}
</script>

<style lang="scss" scoped>
  .zb-card-content {
    position: relative;
    width: px2rem(216);
    height: px2rem(248);
    margin: px2rem(20) auto;
    background-color: #1A2251;
    border-radius: px2rem(16);
    .zb-name {
      position: absolute;
      top: px2rem(30);
      left: 0;
      width: 100%;
      font-size: px2rem(24);
      text-align: center;
      color: #BBC5D2;
    }
    .zb-subname {
      position: absolute;
      top: px2rem(60);
      left: 0;
      width: 100%;
      font-size: px2rem(24);
      text-align: center;
      color: #BBC5D2;
    }
    .zb-value {
      position: absolute;
      top: px2rem(90);
      left: 0;
      font-size: px2rem(44);
      color: #34C9F8;
      font-family: 'HelveticaNeue-Bold';
      width: 100%;
      text-align: center;
    }
    .zb-note {
      position: absolute;
      top: px2rem(172);
      left: 0;
      width: 100%;
      text-align: center;
      font-size: px2rem(22);
      span {
        margin-left: px2rem(5);
      }
    }
    .ring-chart-content {
      position: absolute;
      top: px2rem(40);
      left: 0;
      width: px2rem(200);
      height: px2rem(180);
    }
    .ring-chart-label {
      position: absolute;
      top: px2rem(210);
      left: 0;
      width: 100%;
      height: px2rem(24);
      line-height: px2rem(24);
      font-size: px2rem(22);
      vertical-align: bottom;
      display: flex;
      justify-content: center;
      span {
        &:last-child {
          font-size: px2rem(24);
        }
      }
    }
    .redColor {
      font-weight: bold;
      color: #FF435A;
    }
    .greenColor {
      font-weight: bold;
      color: #8EC644;
    }
    .zb-value3 {
      position: absolute;
      top: px2rem(90);
      left: 0;
      font-size: px2rem(44);
      font-family: 'HelveticaNeue-Bold';
      width: 100%;
      text-align: center;
    }
    .second-row {
      position: absolute;
      top: px2rem(100);
      left: 0;
      font-size: px2rem(44);
      font-family: 'HelveticaNeue-Bold';
      width: 100%;
      text-align: center;
      span {
        &:first-child{
          color: #fff;
          font-size: px2rem(24);
        }
        &:last-child {
          color: #34C9F8;
          font-size: px2rem(44);
        }
      }
    }
    .three-row {
      position: absolute;
      top: px2rem(160);
      left: 0;
      font-size: px2rem(44);
      font-family: 'HelveticaNeue-Bold';
      width: 100%;
      text-align: center;
      span {
        &:first-child{
          color: #fff;
          font-size: px2rem(24);
        }
        &:last-child {
          color: #34C9F8;
          font-size: px2rem(44);
        }
      }
    }
  }
  .zb-card-content-bg {
    background-color: #202D77;
  }

</style>
