<template>
  <div class="home-content">
    <div class="header-content">
      <page-title :title="pageName"></page-title>
      <switch-btn-list :btnList="switchList" @changeType="changeAllType"></switch-btn-list>
    </div>
    <div class="main-content">
      <ZbCard v-for="(item, index) in cardList" :key="index" :zb-obj="item" :order="index"></ZbCard>
    </div>
  </div>
</template>

<script>
import pageTitle from '../../components/common/pageTitle'
import SwitchBtnList from '../../components/common/switchBtnList'
import ZbCard from '../../components/common/zbCard'
import _ from 'lodash'
import {initNum, formatNumber, switchPercent} from '../../assets/js/core/weapon' // 引入工具类方法
import apiService from '../../assets/js/api'
export default {
  name: '',
  data () {
    return {
      pageName: '总体概览',
      switchList: [{ name: '月' }, { name: '季' }, { name: '年累' }, { name: '年' }],
      cardListDataInit: [],
      /** 带环形图的是type2
       *  其他的是type 1
       */
      cardListData: [
        // 第一行
        {zbName: '销售额(万)', subLabel: '同比:', firstValue: '0', secondValue: '0', thirdValue: '', type: '1', needArrow: true, showThreeRow: false, needThreeValue: false},
        {zbName: '子公司达成', subLabel: '目标(万):', firstValue: '0', secondValue: '0', thirdValue: '', type: '2', needArrow: true},
        {zbName: '店铺达成', subLabel: '目标(万):', firstValue: '0%', secondValue: '0', thirdValue: '', type: '2', needArrow: true},
        // 第二行
        {zbName: '回款额(万)', subLabel: '同比:', firstValue: '0', secondValue: '0', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false},
        {zbName: '子公司达成', subLabel: '目标(万):', firstValue: '0', secondValue: '0', thirdValue: '', type: '2', needArrow: true},
        {zbName: '店铺达成', subLabel: '目标(万):', firstValue: '0', secondValue: '0', thirdValue: '', type: '2', needArrow: true},
        // 第三行
        {zbName: '毛利额(万)', subLabel: '同比:', firstValue: '0', secondValue: '0%', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false},
        {zbName: '扣前毛利率', subLabel: '同期:', firstValue: '0%', secondValue: '0', thirdValue: '+0%', type: '1', needArrow: true, showThreeRow: true, needThreeValue: true},
        {zbName: '扣后毛利率', subLabel: '同期:', firstValue: '0%', secondValue: '0', thirdValue: '-0%', type: '1', needArrow: true, showThreeRow: true, needThreeValue: true},
        // 第四行
        {zbName: '利润总额(万)', subLabel: '同期:', firstValue: '0', secondValue: '0', thirdValue: '', type: '1', needArrow: false, showThreeRow: true, needThreeValue: false},
        {zbName: '增减额(万)', subLabel: '同期:', firstValue: '0', secondValue: '0', thirdValue: '', type: '1', needArrow: false, showThreeRow: false, needThreeValue: false},
        {zbName: '增减率', subLabel: '目标(万):', firstValue: '0%', secondValue: '0', thirdValue: '', type: '1', needArrow: false, showThreeRow: false, needThreeValue: false},
        // 第五行
        {zbName: '库存金额(万)', subLabel: '同比:', firstValue: '0.23', secondValue: '0%', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false},
        {zbName: '控制差异率', subLabel: '目标:', firstValue: '0%', secondValue: '0', thirdValue: '', type: '1', needArrow: false, showThreeRow: true, needThreeValue: false},
        {zbName: '周转天数(天)', subLabel: '环比:', firstValue: '0%', secondValue: '0', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false},
        // 第六行
        {zbName: '店铺总数(家)', subLabel: '月增减:', firstValue: '0', secondValue: '+0', thirdValue: '', type: '1', needArrow: false, showThreeRow: true, needThreeValue: false},
        {zbName: '店效(万元)', subLabel: '环比:', firstValue: '0', secondValue: '0%', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false},
        {zbName: '坪效(元)', subLabel: '环比:', firstValue: '0', secondValue: '0%', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false}
      ],
      // 这个用于界面渲染
      cardList: [],
      typeIndex: 0
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    selectedDwName () {
      return this.$store.state.selectedDwName
    }
  },
  methods: {
    changeAllType ({btn, index}) {
      this.typeIndex = index
      this.getCardListData()
    },
    getAllData () {
      this.getCardListData()
    },
    async getCardListData () {
      let url = ''
      if (this.typeIndex === 0 || this.typeIndex === 1) {
        url = apiService.homeCompany.getCardListDataM
      } else if (this.typeIndex === 2 || this.typeIndex === 3) {
        url = apiService.homeCompany.getCardListDataY
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(url,
        {dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, subName: this.selectedDwName})
      if (__statusCode === '1') {
        const result = JSON.parse(data)
        this.cardListDataInit = result
        this.dealCardListData()
      } else {
        alert('获取总览指标数据失败~')
      }
    },
    dealCardListData () {
      let dataList = []
      if (this.typeIndex === 0) {
        dataList = this.cardListDataInit.resM || []
      } else if (this.typeIndex === 1) {
        dataList = this.cardListDataInit.resQ || []
      } else if (this.typeIndex === 2) {
        dataList = this.cardListDataInit.resA || []
      } else if (this.typeIndex === 3) {
        dataList = this.cardListDataInit.resY || []
      }
      const cardListTemp = _.cloneDeep(this.cardListData)
      if (dataList.length > 0) {
        const item = dataList[0]
        cardListTemp[0].firstValue = formatNumber(initNum(item.output_amt), 2) // 扣前销售
        cardListTemp[0].secondValue = switchPercent(item.output_amt_yoy, 1, '%') // 扣前销售 同比
        cardListTemp[1].firstValue = switchPercent(item.yield_rate, 0) // 子公司达成
        cardListTemp[1].secondValue = formatNumber(initNum(item.target_amt), 2) // 子公司 目标
        cardListTemp[2].firstValue = switchPercent(item.yield_rate, 0) // 店铺达成
        cardListTemp[2].secondValue = formatNumber(initNum(item.target_amt), 2) // 店铺 目标

        cardListTemp[3].firstValue = formatNumber(initNum(item.re_amt), 2) // 回款额
        cardListTemp[3].secondValue = switchPercent(item.re_amt_yoy, 1, '%') // 回款额 同比
        cardListTemp[4].firstValue = switchPercent(item.re_yield_rate, 0, '') // 回款额 直营达成
        cardListTemp[4].secondValue = formatNumber(initNum(item.re_target), 2) //  回款额 直营达成 目标
        cardListTemp[5].firstValue = switchPercent(item.re_yield_rate, 0, '') // 回款额 子公司达成
        cardListTemp[5].secondValue = formatNumber(initNum(item.re_target), 2) // 回款额 子公司达成 目标

        cardListTemp[6].firstValue = formatNumber(initNum(item.margin_amt), 2) // 代理价毛利额
        cardListTemp[6].secondValue = switchPercent(item.margin_amt_yoy, 1, '%') // 代理价毛利额 同比
        cardListTemp[7].firstValue = switchPercent(item.margin_rate, 1, '%') // 代理价毛利额 扣前毛利率
        cardListTemp[7].secondValue = switchPercent(item.margin_rate_yoy, 1, '%') //  代理价毛利额 扣前毛利率 同期
        cardListTemp[7].thirdValue = switchPercent(item.margin_rate_deviation, 1, '%') //  代理价毛利额 扣前毛利率 增减
        if (this.typeIndex === 0) {
          cardListTemp[8].firstValue = switchPercent(item.margin_rate_deviation, 1, '%') // 代理价毛利额 扣后毛利率
          cardListTemp[8].secondValue = formatNumber(initNum(item.zgs_re_target), 2) // 代理价毛利额 扣后毛利率 同期
          cardListTemp[8].thirdValue = formatNumber(initNum(item.zgs_re_target), 2) // 代理价毛利额 扣后毛利率 增减
          cardListTemp[8].zbName = '毛利率差异'
          cardListTemp[8].showThreeRow = false
          cardListTemp[8].needArrow = true
        } else {
          cardListTemp[8].firstValue = switchPercent(item.margin_ded_rate, 0, '%') // 代理价毛利额 扣后毛利率
          cardListTemp[8].secondValue = switchPercent(initNum(item.margin_ded_rate_yoy), 1, '%') // 代理价毛利额 扣后毛利率 同期
          cardListTemp[8].thirdValue = switchPercent(initNum(item.margin_ded_rate_deviation), 1, '%') // 代理价毛利额 扣后毛利率 增减
          cardListTemp[8].zbName = '扣后毛利率'
          cardListTemp[8].showThreeRow = true
          cardListTemp[8].needArrow = true
          cardListTemp[8].needThreeValue = true
        }

        cardListTemp[9].firstValue = formatNumber(initNum(item.charge_amt), 2) // 代理价利润
        cardListTemp[9].secondValue = formatNumber(initNum(item.charge_amt_yoy), 2) // 代理价利润同期
        cardListTemp[10].firstValue = formatNumber(initNum(item.charge_deviation), 2) // 代理价利润增减
        cardListTemp[10].secondValue = ''
        cardListTemp[11].firstValue = switchPercent(item.charge_deviation_rate, 1, '%') // 代理价利润增减率
        cardListTemp[11].secondValue = ''

        cardListTemp[12].firstValue = formatNumber(initNum(item.inv_amt), 2) // 库存金额
        cardListTemp[12].secondValue = switchPercent(item.inv_yoy, 1, '%') // 库存同比
        cardListTemp[13].firstValue = switchPercent(item.diff_rate, 1, '%') // 控制差异率
        cardListTemp[13].secondValue = formatNumber(initNum(item.inv_target_amt), 2) // 控制差异率 目标
        cardListTemp[14].firstValue = formatNumber(initNum(item.zz_rate), 2) // 周转天数
        cardListTemp[14].secondValue = switchPercent(item.zz_rate_ring, 1, '%') // 周转天数环比
        cardListTemp[15].firstValue = formatNumber(initNum(item.store_num), 2) // 店铺总数
        cardListTemp[15].secondValue = formatNumber(initNum(item.store_num_yoy), 2) // 年增减
        cardListTemp[16].firstValue = formatNumber(initNum(item.store_sale), 2) // 店效
        cardListTemp[16].secondValue = switchPercent(item.store_sale_ring, 1, '%') // 店效环比
        cardListTemp[17].firstValue = formatNumber(initNum(item.store_area), 2) // 坪效
        cardListTemp[17].secondValue = switchPercent(item.store_area_ring, 1, '%') // 坪效环比
      }
      if (this.typeIndex > 0) {
        this.cardList = cardListTemp
      } else {
        this.cardList = cardListTemp.filter((item, index) => {
          return index < 9 || index > 11
        })
      }
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '总览')
    this.$store.commit('updateShowIcon', false)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '子公司', URL: '总览'})
    if (this.dateStr !== '') {
      this.getAllData()
    }
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  /**
  watch: {
    dateStr: {
      handler () {
        this.getCardListData()
      }
    }
  },**/
  components: {
    pageTitle,
    SwitchBtnList,
    ZbCard
  }
}
</script>

<style lang="scss" scoped>
  .home-content {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .header-content {
      width: 100%;
      height: px2rem(208);
      min-height: px2rem(208);
    }
    .main-content {
      flex: 1;
      width: 100%;
      overflow-y: auto;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-flow:row wrap;
    }

  }

</style>
